<template>
	<view class="page">
		<!-- È°µÈù¢Â§¥ÈÉ® -->
		<view class="page-header">
			<view class="header-content">
				<text class="header-title">‰øÆÊîπÂØÜÁ†Å</text>
			</view>
			<view class="forgot-password-link" @click="goResetPassword">
				<text class="link-text">ÂøòËÆ∞ÂØÜÁ†ÅÔºü</text>
			</view>
		</view>
		
		<!-- Ë°®ÂçïÂç°Áâá -->
		<view class="form-card">
			<!-- ÂΩìÂâçÂØÜÁ†Å -->
			<view class="form-item">
				<view class="form-label">
					<text class="label-text">ÂΩìÂâçÂØÜÁ†Å</text>
					<text class="required" v-if="hasPassword">*</text>
					<text class="optional" v-else>ÔºàÈ¶ñÊ¨°ËÆæÁΩÆÂèØÁïôÁ©∫Ôºâ</text>
				</view>
				<input 
					class="form-input"
					type="password"
					v-model="form.oldPassword"
					:placeholder="hasPassword ? 'ËØ∑ËæìÂÖ•ÂΩìÂâçÂØÜÁ†Å' : 'È¶ñÊ¨°ËÆæÁΩÆÂØÜÁ†ÅÂèØÁïôÁ©∫'"
					:password="!showOldPassword"
				/>
				<view class="password-toggle" @click="showOldPassword = !showOldPassword">
					<text class="toggle-icon">{{ showOldPassword ? 'üëÅÔ∏è' : 'üëÅÔ∏è‚Äçüó®Ô∏è' }}</text>
				</view>
			</view>
			
			<!-- Êñ∞ÂØÜÁ†Å -->
			<view class="form-item">
				<view class="form-label">
					<text class="label-text">Êñ∞ÂØÜÁ†Å</text>
					<text class="required">*</text>
				</view>
				<input 
					class="form-input"
					type="password"
					v-model="form.newPassword"
					placeholder="ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†ÅÔºà‰∏çÂ∞ë‰∫é8‰ΩçÔºåÂåÖÂê´Â§ßÂ∞èÂÜôÂ≠óÊØçÂíåÊï∞Â≠óÔºâ"
					:password="!showNewPassword"
				/>
				<view class="password-toggle" @click="showNewPassword = !showNewPassword">
					<text class="toggle-icon">{{ showNewPassword ? 'üëÅÔ∏è' : 'üëÅÔ∏è‚Äçüó®Ô∏è' }}</text>
				</view>
			</view>
			
			<!-- ÂØÜÁ†ÅÂº∫Â∫¶ÊèêÁ§∫ -->
			<view v-if="form.newPassword" class="password-tips">
				<view class="tip-item" :class="{ 'valid': hasLength }">
					<text class="tip-icon">{{ hasLength ? '‚úì' : '‚óã' }}</text>
					<text class="tip-text">Ëá≥Â∞ë8‰Ωç</text>
				</view>
				<view class="tip-item" :class="{ 'valid': hasUpperCase }">
					<text class="tip-icon">{{ hasUpperCase ? '‚úì' : '‚óã' }}</text>
					<text class="tip-text">ÂåÖÂê´Â§ßÂÜôÂ≠óÊØç</text>
				</view>
				<view class="tip-item" :class="{ 'valid': hasLowerCase }">
					<text class="tip-icon">{{ hasLowerCase ? '‚úì' : '‚óã' }}</text>
					<text class="tip-text">ÂåÖÂê´Â∞èÂÜôÂ≠óÊØç</text>
				</view>
				<view class="tip-item" :class="{ 'valid': hasNumber }">
					<text class="tip-icon">{{ hasNumber ? '‚úì' : '‚óã' }}</text>
					<text class="tip-text">ÂåÖÂê´Êï∞Â≠ó</text>
				</view>
			</view>
			
			<!-- Á°ÆËÆ§Êñ∞ÂØÜÁ†Å -->
			<view class="form-item">
				<view class="form-label">
					<text class="label-text">Á°ÆËÆ§Êñ∞ÂØÜÁ†Å</text>
					<text class="required">*</text>
				</view>
				<input 
					class="form-input"
					type="password"
					v-model="form.confirmPassword"
					placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
					:password="!showConfirmPassword"
				/>
				<view class="password-toggle" @click="showConfirmPassword = !showConfirmPassword">
					<text class="toggle-icon">{{ showConfirmPassword ? 'üëÅÔ∏è' : 'üëÅÔ∏è‚Äçüó®Ô∏è' }}</text>
				</view>
			</view>
			
			<!-- ÂØÜÁ†ÅÂåπÈÖçÊèêÁ§∫ -->
			<view v-if="form.confirmPassword && form.newPassword !== form.confirmPassword" class="error-tip">
				<text class="error-icon">‚ö†Ô∏è</text>
				<text class="error-text">‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥</text>
			</view>
		</view>
		
		<!-- Êèê‰∫§ÊåâÈíÆ -->
		<view class="submit-container">
			<view class="submit-btn" @tap="handleSubmit">
				<text class="submit-text">Á°ÆËÆ§‰øÆÊîπ</text>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			form: {
				oldPassword: '',
				newPassword: '',
				confirmPassword: ''
			},
			showOldPassword: false,
			showNewPassword: false,
			showConfirmPassword: false,
			hasPassword: false // ÊòØÂê¶Â∑≤ËÆæÁΩÆÂØÜÁ†Å
		}
	},
	
	onLoad() {
		// Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶Â∑≤ËÆæÁΩÆÂØÜÁ†Å
		this.checkPasswordStatus()
	},
	
	computed: {
		// ÂØÜÁ†ÅÈïøÂ∫¶Ê£ÄÊü•
		hasLength() {
			return this.form.newPassword.length >= 8
		},
		
		// ÂåÖÂê´Â§ßÂÜôÂ≠óÊØç
		hasUpperCase() {
			return /[A-Z]/.test(this.form.newPassword)
		},
		
		// ÂåÖÂê´Â∞èÂÜôÂ≠óÊØç
		hasLowerCase() {
			return /[a-z]/.test(this.form.newPassword)
		},
		
		// ÂåÖÂê´Êï∞Â≠ó
		hasNumber() {
			return /[0-9]/.test(this.form.newPassword)
		},
		
		// ÂØÜÁ†ÅÂº∫Â∫¶ÊòØÂê¶Êª°Ë∂≥Ë¶ÅÊ±Ç
		isPasswordValid() {
			return this.hasLength && this.hasUpperCase && this.hasLowerCase && this.hasNumber
		}
	},
	
	methods: {
		// Ë∑≥ËΩ¨Âà∞ÈáçÁΩÆÂØÜÁ†ÅÈ°µÈù¢
		goResetPassword() {
			uni.navigateTo({
				url: '/pages-sub/setting/reset-password'
			})
		},
		
		// È™åËØÅÂØÜÁ†ÅÊ†ºÂºè
		validatePassword(password) {
			if (!password) {
				return { valid: false, message: 'ÂØÜÁ†Å‰∏çËÉΩ‰∏∫Á©∫' }
			}
			
			if (password.length < 8) {
				return { valid: false, message: 'ÂØÜÁ†ÅÈïøÂ∫¶‰∏çËÉΩÂ∞ë‰∫é8‰Ωç' }
			}
			
			if (!/[A-Z]/.test(password)) {
				return { valid: false, message: 'ÂØÜÁ†ÅÂøÖÈ°ªÂåÖÂê´Ëá≥Â∞ë‰∏Ä‰∏™Â§ßÂÜôÂ≠óÊØç' }
			}
			
			if (!/[a-z]/.test(password)) {
				return { valid: false, message: 'ÂØÜÁ†ÅÂøÖÈ°ªÂåÖÂê´Ëá≥Â∞ë‰∏Ä‰∏™Â∞èÂÜôÂ≠óÊØç' }
			}
			
			if (!/[0-9]/.test(password)) {
				return { valid: false, message: 'ÂØÜÁ†ÅÂøÖÈ°ªÂåÖÂê´Ëá≥Â∞ë‰∏Ä‰∏™Êï∞Â≠ó' }
			}
			
			return { valid: true, message: 'ÂØÜÁ†ÅÊ†ºÂºèÊ≠£Á°Æ' }
		},
		
		// Êèê‰∫§‰øÆÊîπ
		async handleSubmit() {
			// 1. È™åËØÅÂΩìÂâçÂØÜÁ†ÅÔºàÈ¶ñÊ¨°ËÆæÁΩÆÂØÜÁ†ÅÊó∂ÂèØ‰ª•‰∏∫Á©∫Ôºâ
			// Â¶ÇÊûúÁî®Êà∑Ê≤°ÊúâËÆæÁΩÆÂØÜÁ†ÅÔºåoldPasswordÂèØ‰ª•‰∏∫Á©∫
			// Á≥ªÁªü‰ºöËá™Âä®Âà§Êñ≠ÊòØÈ¶ñÊ¨°ËÆæÁΩÆËøòÊòØ‰øÆÊîπÂØÜÁ†Å
			
			// 2. È™åËØÅÊñ∞ÂØÜÁ†ÅÊ†ºÂºè
			const passwordCheck = this.validatePassword(this.form.newPassword)
			if (!passwordCheck.valid) {
				uni.showToast({
					title: passwordCheck.message,
					icon: 'none',
					duration: 2000
				})
				return
			}
			
			// 3. È™åËØÅÁ°ÆËÆ§ÂØÜÁ†Å
			if (this.form.newPassword !== this.form.confirmPassword) {
				uni.showToast({
					title: '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥',
					icon: 'none'
				})
				return
			}
			
			// 4. È™åËØÅÊñ∞ÂØÜÁ†Å‰∏çËÉΩ‰∏éÊóßÂØÜÁ†ÅÁõ∏ÂêåÔºàÂ¶ÇÊûúÂ∑≤ËÆæÁΩÆÂØÜÁ†ÅÔºâ
			if (this.hasPassword && this.form.oldPassword && this.form.oldPassword === this.form.newPassword) {
				uni.showToast({
					title: 'Êñ∞ÂØÜÁ†Å‰∏çËÉΩ‰∏éÂΩìÂâçÂØÜÁ†ÅÁõ∏Âêå',
					icon: 'none'
				})
				return
			}
			
			// 5. Êèê‰∫§‰øÆÊîπ
			uni.showLoading({ title: '‰øÆÊîπ‰∏≠...', mask: true })
			
			try {
				const result = await wx.cloud.callFunction({
					name: 'changePassword',
					data: {
						oldPassword: this.form.oldPassword,
						newPassword: this.form.newPassword
					}
				})
				
				uni.hideLoading()
				
				if (result.result && result.result.success) {
					uni.showToast({
						title: 'ÂØÜÁ†Å‰øÆÊîπÊàêÂäü',
						icon: 'success'
					})
					
					// Ê∏ÖÁ©∫Ë°®Âçï
					this.form = {
						oldPassword: '',
						newPassword: '',
						confirmPassword: ''
					}
					
					// Âª∂ËøüËøîÂõû
					setTimeout(() => {
						uni.navigateBack()
					}, 1500)
				} else {
					throw new Error(result.result?.message || 'ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•')
				}
			} catch (err) {
				uni.hideLoading()
				console.error('ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•:', err)
				uni.showToast({
					title: err.message || 'ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•',
					icon: 'none',
					duration: 2000
				})
			}
		}
	}
}
</script>

